import React, { Component } from 'react';
import Navigation from './Navigation/Navigation'
import './App.css';
import SideMenu from './SideMenu/SideMenu'
import Home from './Home/Home'
import Decks from './Decks/Decks'
import Stats from './Stats/Stats'
import About from './About/About'

const helpers = {
  selectOption: function(i) {
      document.querySelectorAll('.menu-item').forEach((item, index) => {
          item.classList.remove('menu-item-clicked')
          if (index === i) {
              item.classList.add('menu-item-clicked')
          }
      })
  }
}

class App extends Component {
  constructor() {
    super()
    this.state = {
      menuOption: "home"
    }
  }

  setSelectedItem = (option) => {
    this.setState({menuOption: option}, ()=>{this.selectItem()});    
  }

  selectItem = () => {
    switch(this.state.menuOption) {
      case "home":
        helpers.selectOption(0)
        break;
      case "decks":
        helpers.selectOption(1)
        break;
      case "stats":
        helpers.selectOption(2)
        break;
      case "about":
        helpers.selectOption(3)
        break;
      default: 
        break;   
    }

  }
  
  render(){
    const { menuOption } = this.state;
    const page = (menuOption === 'home') ? <Home /> :
      ((menuOption === 'decks') ? <Decks /> :
        ((menuOption === 'stats') ? <Stats /> :
          ((menuOption === 'about') ? <About /> : <Home />
          )
        )
      )

    return (
      <div className="df f-d-column">
        <Navigation />
        <div className="f-f-g-1 df f-a-i-stretch">
          <SideMenu setSelectedItem = {this.setSelectedItem} selectItem={this.selectItem}/>
          {page}
        </div>
      </div>
    );
  }
}

export default App;